import {
  Link,
} from "react-router-dom";
import Book from "../book"
import removeAccents from "./removeAccents"
import { bookData } from "../../assets/book"
import noresults from "../../images/noresults.webp"

export default function SearchList({ name }) {
  const searchName = removeAccents(name)
  const regex = RegExp(searchName)
  const bookFound = bookData.filter(items => regex.test(removeAccents(items.name))).length

  return (
    <div className="w-full">
      <div className="flex justify-between items-center">
        <p className="text-lg">Hiển thị {bookFound} kết quả</p>
        <p>Từ khóa tìm kiếm: {name}</p>
      </div>
      <div className="grid grid-cols-3 pt-8">
        {bookFound !== 0
          ? bookData.filter(items => regex.test(removeAccents(items.name))).map(items => {
            return (
              <Book key={items.id} id={items.id} photo={items.image} name={items.name} author={items.author} price={items.price} sale={items.sale} />
            )
          })
          : <div className="col-span-3 flex flex-col justify-center items-center">
            <img src={noresults} alt="noData" className="w-3/4 h-auto" />
            <Link to="/categories">
              <div className="w-40 h-12 flex justify-center items-center bg-black">
                <p className="text-white">Trở về</p>
              </div>
            </Link>
          </div>}
      </div>
    </div>
  )
}