import Book from "../book"
import { bookData } from "../../assets/book"

export default function BookList({ cat }) {
  const category = cat === undefined
  var categoryShow = ""
  switch (cat) {
    case "literature":
      categoryShow = "Văn học"
      break
    case "edu":
      categoryShow = "Giáo dục"
      break
    case "selfhelp":
      categoryShow = "Kỹ năng sống"
      break
    case "other":
      categoryShow = "Khác"
      break
    default:
      categoryShow = ""
      break
  }

  return (
    <div className="w-full">
      <div className="flex justify-between items-center">
        <p className="text-lg">Hiển thị {category ? bookData.length : bookData.filter(items => items.cat === cat).length} kết quả</p>
        <p>Danh mục {category ? "" : `> ${categoryShow}`}</p>
      </div>
      <div className="grid grid-cols-3 pt-8">
        {category ? bookData.map(items => {
          return (
            <Book key={items.id} id={items.id} photo={items.image} name={items.name} author={items.author} price={items.price} sale={items.sale} />
          )
        }) : bookData.filter(items => items.cat === cat).map(items => {
          return (
            <Book key={items.id} id={items.id} photo={items.image} name={items.name} author={items.author} price={items.price} sale={items.sale} />
          )
        })}
      </div>

      <div className="bg-white px-4 py-3 flex items-center justify-center sm:px-6 pt-10">
        <div className="flex items-center justify-center">
          <nav className="relative z-0 inline-flex shadow-sm -space-x-px" aria-label="Pagination">
            <a href="/categories" className="relative inline-flex items-center px-4 py-2 rounded-l-md border border-gray-300 bg-white text-sm font-medium hover:bg-gray-50">
              Trước
              </a>
            <a href="/categories" className="relative inline-flex items-center px-4 py-2 border border-gray-300 bg-white text-sm font-medium text-gray-700 hover:bg-gray-50">
              1
        </a>
            <a href="/categories" className="relative inline-flex items-center px-4 py-2 border border-gray-300 bg-white text-sm font-medium text-gray-700 hover:bg-gray-50">
              2
        </a>
            <span className="relative inline-flex items-center px-4 py-2 border border-gray-300 bg-white text-sm font-medium text-gray-700">
              ...
              </span>
            <a href="/categories" className="relative inline-flex items-center px-4 py-2 rounded-r-md border border-gray-300 bg-white text-sm font-medium hover:bg-gray-50">
              Sau
              </a>
          </nav>
        </div>
      </div>
    </div>
  )
}