import CartItem from "./CartItem"
import { useSelector } from 'react-redux'

export default function BookCart() {
  const bookState = useSelector(state => state.cart)

  return (
    <div className="w-full bg-white">
      <div className="grid grid-cols-5 gap-4 p-8 border-b border-solid border-gray-200">
        <div className="col-span-2">
          <p className="font-semibold">Tên sản phẩm</p>
        </div>
        <p className="col-span-1 font-semibold text-center">Giá tiền</p>
        <p className="col-span-1 font-semibold text-center">Số lượng</p>
        <p className="col-span-1 font-semibold text-center">Xóa</p>
      </div>
      {bookState !== [] && bookState.map(items => {
        const data = items.product
        return (
          <CartItem key={data.id} id={data.id} photo={data.image} name={data.name} author={data.author} price={data.price} quantity={items.quantity} />
        )
      })}
    </div>
  )
}