import React, { useState } from "react"
import {
  Link,
  useHistory,
} from "react-router-dom";
import Logo from './logo.svg';
import user from '../../images/image2.png';
import cart from '../../images/image3.png';
import search from '../../images/image4.png';
import { useSelector } from 'react-redux'


function RouteChange(history, searchData) {
  let path = searchData;
  if (searchData.trim() !== "") {
    history.push(`/search/${path}`);
  }
}

export default function Header() {
  let history = useHistory();
  const [searchData, setsearchData] = useState("")
  const bookState = useSelector(state => state.cart)

  return (
    <div className="container mx-auto w-full py-6 flex justify-between">
      <div className="left-side flex items-center">
        <Link to="/"><img src={Logo} alt="logo" /></Link>
        <Link to="/"><p className="pl-10">Trang chủ</p></Link>
        <Link to="/categories"><p className="pl-10">Danh mục</p></Link>
        <Link to="/contact"><p className="pl-10">Liên hệ</p></Link>
        <Link to="/help"><p className="pl-10">Hỗ trợ</p></Link>
      </div>
      <div className="right-side flex items-center">
        <div className="relative flex ">
          <input type="text" value={searchData} onChange={event => setsearchData(event.target.value)} className="h-12 w-64 bg-gray-200 p-2 pl-12 flex items-center focus:outline-none border border-gray-200 px-4" onKeyUp={(event) => {
            if (event.key === "Enter") {
              RouteChange(history, searchData)
            }
          }} >
          </input>
          <img src={search} alt="logo" className="h-8 absolute m-2" onClick={() => RouteChange(history, searchData)} />
        </div>
        <Link to="/login">
          <img src={user} alt="logo" className="h-5 pl-10" />
        </Link>
        <Link to="/cart">
          <div className="h-6 pl-10 relative">
            <img src={cart} alt="logo" className="h-6" />
            {bookState.length !== 0 ?
              <div className="text-xs absolute -top-1 -right-1.5 rounded-full bg-black text-white h-4 w-4 text-center">{bookState.length}</div>
              : <></>}
          </div>
        </Link>
      </div>
    </div>
  )
}