import { useDispatch } from 'react-redux'
import { removeFromCart } from '../../actions'

export default function CartItem({ id, photo, name, author, price, quantity }) {
  const dispatch = useDispatch()

  return (
    <div className="grid grid-cols-5 gap-4 p-8 border-b border-solid border-gray-200">
      <div className="col-span-1 flex justify-center">
        <img src={`${process.env.PUBLIC_URL}${photo}`} alt="book" className="h-48 w-full object-scale-down" />
      </div>
      <div className="col-span-1 flex flex-col justify-center">
        <p className="text-lg font-medium leading-6">{name}</p>
        <p className="text-sm text-gray-400 mt-2">{author}</p>
      </div>
      <div className="col-span-1 flex flex-col justify-center items-center">
        <p>{price}</p>
      </div>
      <div className="col-span-1 flex flex-col justify-center items-center">
        <p>{quantity}</p>
      </div>
      <div className="col-span-1 flex flex-col justify-center items-center">
        <p onClick={() => dispatch(removeFromCart(id))}>Remove</p>
      </div>
    </div>
  )
}