import { bookData } from '../assets/book'
import { addItemToCart } from './cart.utils'
const initialState = []

const cartReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD_TO_CART":
      return addItemToCart(state, bookData.filter(items => items.id === action.id)[0])

    case "REMOVE_FROM_CART":
      return state.filter(items =>
        items.product.id !== action.id
      )

    // case "ADD_QUANTITY":
    //   return {
    //     ...state,
    //     products: bookData.map(items =>
    //       items.id === action.id
    //         ? { ...items, quantity: items.quantity + 1 }
    //         : items,
    //     ),
    //   };
    // case "SUB_QUANTITY":
    //   return {
    //     ...state,
    //     products: bookData.map(items =>
    //       items.id === action.id
    //         ? {
    //           ...items,
    //           quantity: items.quantity !== 1 ? items.quantity - 1 : 1,
    //         }
    //         : items,
    //     ),
    //   };
    default:
      return state;
  }
};
export default cartReducer;